.app-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  /* Add padding to keep content away from corners */
  padding: 20px;
  box-sizing: border-box;
}

.main-content {
  display: flex;
  flex: 1;
  /* Add margin to keep content away from edges */
  margin: 10px 0;
  border-radius: 8px;
  overflow: hidden;
}

.editor-container {
  flex: 1;
  border-right: 1px solid #ccc;
  display: flex;
  flex-direction: column;
}

.dry-run-container {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.controls {
  display: flex;
  padding: 15px;
  background-color: #f0f0f0;
  border-radius: 8px;
  margin-bottom: 10px;
  /* Ensure controls aren't stuck in corners */
  justify-content: center;
  flex-wrap: wrap;
  gap: 10px;
}

.controls button {
  margin: 0 5px;
  padding: 8px 15px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.controls button:hover {
  background-color: #45a049;
}

.language-selector {
  margin: 0 10px;
}

.terminal-container {
  height: 180px;
  background-color: #1e1e1e;
  color: white;
  padding: 15px;
  font-family: monospace;
  overflow-y: auto;
  /* Add rounded corners to keep content away from screen corners */
  border-radius: 8px;
  margin-top: 10px;
}

.highlighted-line {
  background-color: rgba(255, 255, 0, 0.3);
}

/* Make sure interactive elements have adequate spacing */
button, select, input {
  min-height: 36px;
  min-width: 36px;
}